}

.shadow-4 {
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}

.shadow-5 {
  box-shadow: 0 19px 38px rgba(0,0,0,0.30), 0 15px 12px rgba(0,0,0,0.22);
}
.progress{
  /* padding-bottom: 1rem; */
  height: 3px;
  /* position: relative; */
  background-color:#343a4000; 
  /* position: fixed; */
  top: 0;
  left: 0; 
}

.progress .progress-bar{
 /* position: absolute; */
 height: 3px;
 width: 20%;
 background-color:#0277bd;
 border-radius:5px; 
 animation: progress-animation 1s ease-in-out infinite alternate-reverse both;
}

@-webkit-keyframes progress-animation{
0% {
  -webkit-transform: translateX(-1rem);
  transform: translateX(-1rem);
}
100% {
  -webkit-transform: translateX(14.5rem);
  transform: translateX(14.5rem);
}
}

@keyframes progress-animation{
0% {
  -webkit-transform: translateX(-1rem);
  transform: translateX(-1rem);
}
100% {
  -webkit-transform: translateX(14.5rem);
  transform: translateX(14.5rem);
}
}


.text-expand {
-webkit-animation: text-expand 0.7s cubic-bezier(0.215, 0.610, 0.355, 1.000) 0.2s both;
        animation: text-expand 0.7s cubic-bezier(0.215, 0.610, 0.355, 1.000) 0.2s both;
}

@-webkit-keyframes text-expand {
0% {
  letter-spacing: -0.5em;
  opacity: 0;
}
40% {
  opacity: 0.6;
}
100% {
  opacity: 1;
}
}

@keyframes text-expand {
0% {
  letter-spacing: -0.5em;
  opacity: 0;
}
40% {
  opacity: 0.6;
}
100% {
  opacity: 1;
}
}

.fade-in {
-webkit-animation: fade-in 0.6s cubic-bezier(0.390, 0.575, 0.565, 1.000) 0.47s both;
        animation: fade-in 0.6s cubic-bezier(0.390, 0.575, 0.565, 1.000) 0.47s both;
}
@-webkit-keyframes fade-in {
0% {
  -webkit-transform: translateX(-50px);
          transform: translateX(-50px);
  opacity: 0;
}
100% {
  -webkit-transform: translateX(0);
          transform: translateX(0);
  opacity: 1;
}
}
@keyframes fade-in {
0% {
  -webkit-transform: translateX(-50px);
          transform: translateX(-50px);
  opacity: 0;
}
100% {
  -webkit-transform: translateX(0);
          transform: translateX(0);
  opacity: 1;
}
}
@-webkit-keyframes attention {
0% {
  -webkit-transform: translateX(-20px);
          transform: translateX(-20px);
  -webkit-animation-timing-function: ease-in;
          animation-timing-function: ease-in;
  opacity: 1;
}
24% {
  opacity: 1;
}
40% {
  -webkit-transform: translateX(-26px);
          transform: translateX(-26px);
  -webkit-animation-timing-function: ease-in;
          animation-timing-function: ease-in;
}
65% {
  -webkit-transform: translateX(-13px);
          transform: translateX(-13px);
  -webkit-animation-timing-function: ease-in;
          animation-timing-function: ease-in;
}
82% {
  -webkit-transform: translateX(-6.5px);
          transform: translateX(-6.5px);
  -webkit-animation-timing-function: ease-in;
          animation-timing-function: ease-in;
}
93% {
  -webkit-transform: translateX(-4px);
          transform: translateX(-4px);
  -webkit-animation-timing-function: ease-in;
          animation-timing-function: ease-in;
}
25%,
55%,
75%,
87%,
98% {
  -webkit-transform: translateX(0px);
          transform: translateX(0px);
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
}
100% {
  -webkit-transform: translateX(0px);
          transform: translateX(0px);
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  opacity: 1;
}
}
@keyframes attention {
0% {
  -webkit-transform: translateX(-20px);
          transform: translateX(-20px);
  -webkit-animation-timing-function: ease-in;
          animation-timing-function: ease-in;
  opacity: 1;
}
24% {
  opacity: 1;
}
40% {
  -webkit-transform: translateX(-26px);
          transform: translateX(-26px);
  -webkit-animation-timing-function: ease-in;
          animation-timing-function: ease-in;
}
65% {
  -webkit-transform: translateX(-13px);
          transform: translateX(-13px);
  -webkit-animation-timing-function: ease-in;
          animation-timing-function: ease-in;
}
82% {
  -webkit-transform: translateX(-6.5px);
          transform: translateX(-6.5px);
  -webkit-animation-timing-function: ease-in;
          animation-timing-function: ease-in;
}
93% {
  -webkit-transform: translateX(-4px);
          transform: translateX(-4px);
  -webkit-animation-timing-function: ease-in;
          animation-timing-function: ease-in;
}
25%,
55%,
75%,
87%,
98% {
  -webkit-transform: translateX(0px);
          transform: translateX(0px);
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
}
100% {
  -webkit-transform: translateX(0px);
          transform: translateX(0px);
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  opacity: 1;
}
}

